<template>
    <div class="box">
      <div class="section_left">
        <index-left></index-left>
      </div>
      <section-right></section-right>
    </div>
</template>

<script>
    import SectionRight from "../components/index/SectionRight";
    import IndexLeft from "../components/index/IndexLeft";
    export default {
        name: "Index",
      components: {IndexLeft, SectionRight}
    }
</script>

<style scoped>
  .box{
    width: 100%;
   position: relative;
  }
  .section_left{
    width: 250px;
    float: left;
    height: 100%;
    position: absolute;
  }
</style>



