<template>
    <div class="section_right">
        <index-header></index-header>
        <section-one></section-one>
      <section-three></section-three>
      <section-dates></section-dates>
      <section-two></section-two>
      <section-botton></section-botton>
    </div>
</template>

<script>
    import SectionOne from "./SectionOne";
    import SectionTwo from "./SectionTwo";
    import SectionThree from "./SectionThree";
    import IndexHeader from "./IndexHeader";
    import SectionDates from "./SectionDates";
    import SectionBotton from "./SectionBotton";
    export default {
        name: "SectionRight",
      components: {SectionBotton, SectionDates, IndexHeader, SectionThree, SectionTwo,SectionOne}
    }
</script>

<style scoped>
  .section_right{
    width: 100%;
    padding: 10px 45px 0 295px;
    box-sizing: border-box;
    background: url("../../../static/images/mainface.png") no-repeat left top;
    background-size: cover;
  }
</style>



