<template>

  <section>

    <div class="leftdrop">

      <ul class="leftdrop-ul" >
        <li @click="meA">
          <i class="iconfont icon-ren"></i>
          我的主页
          <i class="iconfont icon-Group-"></i>
        </li>
        <drop-one v-show="isShowA"></drop-one>
        <li @click="meB">
          <i class="iconfont icon-quanbudingdan"></i>
          商品列表
          <i class="iconfont icon-Group-"></i>
        </li>
        <drop-two v-show="isShowB"></drop-two>
        <li @click="meC">
          <i class="iconfont icon-daishouhuo"></i>
          销售记录
          <i class="iconfont icon-Group-"></i>
        </li>
        <drop-there v-show="isShowC"></drop-there>
        <li @click="meD">
          <i class="iconfont icon-weipinhui"></i>
          应用
          <i class="iconfont icon-Group-"></i>
        </li>
        <drop-four v-show="isShowD"></drop-four>
        <li @click="meE">
          <i class="iconfont icon-shaixuan"></i>
          高级
          <i class="iconfont icon-Group-"></i>
        </li>
        <drop-five v-show="isShowE"></drop-five>
        <li @click="meF">
          <i class="iconfont icon-daipingjiaicon"></i>
          用户
          <i class="iconfont icon-Group-"></i>
        </li>
        <drop-six v-show="isShowF"></drop-six>
        <li @click="meG">
          <i class="iconfont icon-shezhi"></i>
          设置
          <i class="iconfont icon-Group-"></i>
        </li>
        <drop-seven v-show="isShowG"></drop-seven>
        <li >
          <i class="iconfont icon-huiyuanjulebu"></i>
          授权

        </li>

      </ul>
    </div>
  </section>

</template>

<script>
  import DropOne from "../Leftdrop/DropOne";
  import DropTwo from "../Leftdrop/DropTwo";
  import DropThere from "../Leftdrop/DropThere";
  import DropFour from "../Leftdrop/DropFour";
  import DropFive from "../Leftdrop/DropFive";
  import DropSix from "../Leftdrop/DropSix";
  import DropSeven from "../Leftdrop/DropSeven";
  export default {
    name: "IndexLeft",
    components: { DropSeven, DropSix, DropFive, DropFour, DropThere, DropTwo, DropOne},
    data(){
      return{
        isShowA:false,
        isShowB:false,
        isShowC:false,
        isShowD:false,
        isShowE:false,
        isShowF:false,
        isShowG:false,
      }
    },
    methods:{
      meA(){
        this.isShowA=!this.isShowA;
      },
      meB(){
        this.isShowB=!this.isShowB;
      },
      meC(){
        this.isShowC=!this.isShowC;
      },
      meD(){
        this.isShowD=!this.isShowD;
      },
      meE(){
        this.isShowE=!this.isShowE;
      },
      meF(){
        this.isShowF=!this.isShowF;
      },
      meG(){
        this.isShowG=!this.isShowG;
      },
    }
  }
</script>

<style scoped>
  section{
    height: 100%;
  }
  .leftdrop {
    width: 250px;
    height: 100%;
    background-color: #151515;
    color: #959595;
    line-height: 60px;
    font-size: 18px;

  }

  .leftdrop h3 {
    text-align: center;
    height: 7%;
    color: white;
    border-bottom: 1px solid #666666;
  }

  .leftdrop-ul {
    height: 97%;
  }
  .leftdrop-ul li{
    padding-left: 20px;
  }
.iconfont{
  margin-right: 15px;
}
  .icon-Group-{
    float: right;
    margin-right: 35px;
  }
 ul li:hover{
   border-left:5px solid deeppink;
   margin: 0;
   padding-left: 15px;
   color: white;
 }
</style>



