<template>
  <ul >
    <li v-for="(n,index) in arry" :class="{ red:changeRed == index}" @click="change(index)">
      {{n.tit}}
    </li>
  </ul>
</template>

<script>
  export default {
    name: "DropFive",
    data(){
      return{
        arry:[
          {
            tit:"奢侈品"
          },
          {
            tit:'短视频'
          },
          {
            tit:"历史纪录"
          }
        ],
        changeRed:-1
      }
    },
    methods:{
      change(index){
        this.changeRed = index;
      }
    }

  }
</script>

<style scoped>
  ul{
    background-color: #040303;
  }
  ul li{
    padding-left: 70px;
    font-size: 14px;

  }
  .red{
    color: white;
    background: #009688;
  }
</style>



