<template>
  <section>

    <div class="box">
      <h1>管理员登录</h1>
      <div class="logo">
        <img src="../../static/images/logo.png" alt="">
      </div>
      <div class="error"></div>
      <div class="form">
        <div class="userName">
          <input type="text" placeholder="手机号/邮箱/会员名" id="userName">
        </div>
        <div class="userPass">
          <input type="password" placeholder="请输入密码" id="userPass">
        </div>
      </div>

      <div class="login">
        <a href="#" @click="login()">登录</a>
      </div>
    </div>
  </section>

</template>

<script>
  import $ from 'jquery'

  export default {
    name: "Login",
    methods: {
      login: function () {
        if ($("#userName").val() == "VbeeAdmin" && $("#userPass").val() == "123456") {
         this.$router.push("/index")
        } else {
          if ($("#userName").val() == "") {
            $('.error').html("请输入手机号/邮箱/会员名")
          }
          if ($("#userPass").val() == "") {
            $('.error').html("请输入密码")
          }
          $('.error').html("此用户不存在！请注册")
        }
      }
    },
    mounted: function () {
      this.$nextTick(() => {
        $("#userName").blur(function () {
          let $name = $(this).val()
          if ($name.length > 0) {
            let pattern = /^1[345789]\d{9}$/

            let pattern1 = /[a-zA-Z0-9]{1,10}@[a-zA-Z0-9]{1,5}\.[a-zA-Z0-9]{1,5}/

            let pattern2 = /^[0-9a-zA-Z|_]{6,20}$/g
            // 长度6-20个字符，只能包括字母、数字、下划线
            if (pattern.test($name) || pattern1.test($name) || pattern2.test($name)) {
              $('.error').html("")
            } else {
              $('.error').html("请输入正确的手机号/邮箱/会员名");
            }
          } else {
            $('.error').html("请输入手机号/邮箱/会员名")
          }
        })

        $("#userPass").blur(function () {
          let $pwd = $(this).val()
          if ($pwd.length > 0) {
            if ($pwd.length < 6 || $pwd.length > 16) {
              $('.error').html("请输入6-16位密码")
            } else {
              let pattern = /^[a-z0-9_-]{6,16}$/
              if (pattern.test($pwd)) {
                $('.error').html("")
              } else {
                $('.error').html("密码格式错误，密码格式为6-16位字母/数字/符号")
              }
            }
          } else {
            $('.error').html("请输入密码")
          }
        })
      })
    }
  }
</script>
<style scoped>
  section{
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: url("../../static/images/b1f1ecfcd9047e09986f33d2ddfc81f6.jpg") -200px;
    background-size: cover;
  }
  .box {
    text-align: center; /*让div内部文字居中*/
    border-radius: 20px;
    width: 350px;
    height: 350px;
    margin: auto;
    position: absolute;
    top: -100px;
    left: 800px;
    right: 0;
    bottom: 0;
    background-color: #fff;
  }
  .box h1{
    color: red;
  }
  .logo img {
    width: 150px;
    height: 150px;
  }

  .error {
    height: .27rem;
    color: red;
    font-size: .14rem;
    line-height: .27rem;
    text-indent: .1rem;
  }

  input {
    width: 200px;
    height: 30px;
    margin-top: 15px;
  }


  .login a:first-child {
    display: block;
    background-color: aqua;
    width: 200px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    color: black;
    font-size: 16px;
    margin: 15px auto;
    border-radius: 5px;
  }


  .login a:last-child {
    display: block;
    margin-top: 15px;
    font-size: 18px;
    color: black;
  }
</style>



