<template>
  <ul >
    <li v-for="(n,index) in arry" :class="{ red:changeRed == index}" @click="change(index)">
      {{n.tit}}
    </li>
  </ul>
</template>

<script>
  export default {
    name: "DropSeven",
    data(){
      return{
        arry:[
          {
            tit:"系统设置"
          },
          {
            tit:'网站设置'
          },
          {
            tit:"邮件服务"
          },
          {
            tit:"基本资料"
          },
          {
            tit:"修改密码"
          }

        ],
        changeRed:-1
      }
    },
    methods:{
      change(index){
        this.changeRed = index;
      }
    }

  }
</script>

<style scoped>
  ul{
    background-color: #040303;
  }
  ul li{
    padding-left: 70px;
    font-size: 14px;

  }
  .red{
    color: white;
    background: #009688;
  }
</style>



