<template>
  <section>


    <div class="rightAdmin">
      <input type="text" placeholder="搜索...">
      <div class="user">
        <a href="javascript:void(0)">
          <img src="../../../static/images/icon_1.jpg"/>
        </a>
        <a href="javascript:void(0)">
          <img src="../../../static/images/icon_2.jpg"/>
        </a>
        <a href="javascript:void(0)">
          <img src="../../../static/images/icon_3.jpg"/>
        </a>

        <a href="javascript:void(0)" @click="handleFullScreen">
          <el-tooltip effect="dark" :content="fullscreen ? `取消全屏`:`全屏`" placement="bottom">

            <img src="../../../static/images/icon_4.jpg"/>
          </el-tooltip>
        </a>

        <a class="Admin" href="javascript:void(0)" @click="user">
          Admin<img src="../../../static/images/icon_5.jpg"/>
          <ul class="uesrname"  v-show="name">
            <li>基本资料</li>
            <li>修改密码</li>
            <li>退出</li>
          </ul>
        </a>

        <a href="javascript:void(0)">
          <img src="../../../static/images/icon_6.jpg"/>
        </a>
      </div>

    </div>
  </section>
</template>

<script>
  export default {
    name: "IndexHeader",

    data() {
      return {
        fullscreen: false,
        name:false
      }
    },
    methods: {
      handleFullScreen() {
        let element = document.documentElement;
        if (this.fullscreen) {
          if (document.exitFullscreen) {
            document.exitFullscreen();
          } else if (document.webkitCancelFullScreen) {
            document.webkitCancelFullScreen();
          } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen();
          } else if (document.msExitFullscreen) {
            document.msExitFullscreen();
          }
        } else {
          if (element.requestFullscreen) {
            element.requestFullscreen();
          } else if (element.webkitRequestFullScreen) {
            element.webkitRequestFullScreen();
          } else if (element.mozRequestFullScreen) {
            element.mozRequestFullScreen();
          } else if (element.msRequestFullscreen) {
            // IE11
            element.msRequestFullscreen();
          }
        }
        this.fullscreen = !this.fullscreen;
      },
      user(){
          this.name=!this.name;
      }

    }
  }
</script>

<style scoped>
  section {
    width: 100%;
    height: 50px;
    background: white;
    display: flex;
    margin-top: 20px;
    min-width: 850px;
    border: 1px solid gray;
    box-sizing: border-box;
  }


  .rightAdmin {
    width: 100%;
    padding-left: 50px;
    box-sizing: border-box;
  }

  .rightAdmin input {
    height: 30px;
    outline: none;
    font-size: 16px;
    border: none;
    margin-top: 10px;
  }

  .user {
    float: right;
    line-height: 50px;
    margin-right: 25px;
  }

  .user a {
    display: inline-block;
    color: #000;
    padding: 0 20px;
  }
  .user a:nth-child(2) img{
    margin-top: -5px;
  }
  .user a img{
    width: 25px;
    height: 25px;
    vertical-align: middle;
  }
  .Admin{
    position: relative;
  }
  .uesrname{
    width: 125px;
    height: 200px;
    background-color: white;
    border-radius: 0 0 15px 15px;
    text-align: center;
    position: absolute;
    left: -30px;
    top: 35px;
  }
</style>



