<template>
  <ul>
    <li v-for="(n,index) in arry" :class="{ red:changeRed === index}" @click="change(index)">
      {{n.tit}}
    </li>
  </ul>
</template>

<script>
  export default {
    name: "DropTwo",
    data() {
      return {
        arry: [
          {
            tit: "男衣"
          },
          {
            tit: '女衣'
          },
          {
            tit: "男鞋"
          },
          {
            tit: "女鞋"
          },
          {
            tit: "裤子"
          },
          {
            tit: "内衣"
          }
        ],
        changeRed: -1
      }
    },
    methods: {
      change(index) {
        this.changeRed = index;
      }
    }

  }
</script>

<style scoped>
  ul {
    background-color: #040303;
  }

  ul li {
    padding-left: 70px;
    font-size: 14px;

  }

  .red {
    color: white;
    background: #009688;
  }
</style>



