<template>
    <div id="section_three">
    </div>
</template>

<script>

    export default {
        name: "SectionThree",
      mounted(){
        this.ech()

      },
      methods: {
        ech() {
          // 绘制图表
          // Generate data
          var category = [];
          var dottedBase = [];
          var lineData = [18092, 20728, 24045, 28348, 32808, 36097, 39867, 44715, 48444, 50415, 56061, 62677, 69521, 77560, 85038, 92477, 102268];
          var barData = [4600, 5000, 5500, 6500, 7500, 8500, 9900, 12500, 14000, 21500, 23200, 24450, 25250, 33300, 35800, 45400, 59810];
          var rateData = [];

          for (var i = 0; i < 12; i++) {
            var date = i + 1;
            category.push(date)
            var rate = barData[i] / lineData[i];
            rateData[i] = rate.toFixed(2);
          }


// option
         let option = {
            title: {
              text: '唯品会商品进出库数量图',
              x: 'center',
              y: 20,
              textStyle: {
                color: '#B4B4B4',
                fontSize: 20,
                fontWeight: '600',
              },

            },
            backgroundColor: '#191E40',
            tooltip: {
              trigger: 'axis',
              backgroundColor: 'rgba(255,255,255,0.1)',
              axisPointer: {
                type: 'shadow',
                label: {
                  show: true,
                  backgroundColor: '#7B7DDC'
                }
              }
            },
            legend: {
              data: ['商品进库数量', '商品出库数量', '商品损坏数量',],
              textStyle: {
                color: '#288ce2'
              },
              top: '15%',
            },
            grid: {
              x: '12%',
              width: '80%',
              y: '20%',
            },
            xAxis: {
              data: category,
              axisLine: {
                lineStyle: {
                  color: '#B4B4B4'
                }
              },
              axisTick: {
                show: false,
              },
            },
            yAxis: [{

              splitLine: {
                show: false
              },
              axisLine: {
                lineStyle: {
                  color: '#B4B4B4',
                }
              },

              axisLabel: {
                formatter: '{value} Money',
              }
            },
              {

                splitLine: {
                  show: false
                },
                axisLine: {
                  lineStyle: {
                    color: '#B4B4B4',
                  }
                },
                axisLabel: {
                  formatter: '{value} 利润',
                }
              }
            ],

            series: [{
              name: '商品损坏数量',
              type: 'line',
              smooth: true,
              showAllSymbol: true,
              symbol: 'emptyCircle',
              symbolSize: 8,
              yAxisIndex: 1,
              itemStyle: {
                normal: {
                  color: '#F02FC2'
                },
              },
              data: rateData
            },

              {
                name: '商品出库数量',
                type: 'bar',
                barWidth: 10,
                itemStyle: {
                  normal: {
                    barBorderRadius: 5,
                    color: new this.$echarts.graphic.LinearGradient(
                      0, 0, 0, 1,
                      [{
                        offset: 0,
                        color: '#956FD4'
                      },
                        {
                          offset: 1,
                          color: '#3EACE5'
                        }
                      ]
                    )
                  }
                },
                data: barData
              },

              {
                name: '商品进库数量',
                type: 'bar',
                barGap: '-100%',
                barWidth: 10,
                itemStyle: {
                  normal: {
                    barBorderRadius: 5,
                    color: new this.$echarts.graphic.LinearGradient(
                      0, 0, 0, 1,
                      [{
                        offset: 0,
                        color: 'rgba(156,107,211,0.5)'
                      },
                        {
                          offset: 0.2,
                          color: 'rgba(156,107,211,0.3)'
                        },
                        {
                          offset: 1,
                          color: 'rgba(156,107,211,0)'
                        }
                      ]
                    )
                  }
                },
                z: -12,

                data: lineData
              },
            ]
          };
          // 基于准备好的dom，初始化echarts实例
          let myChart = this.$echarts.init(document.getElementById('section_three'))
          myChart.setOption(option)
         window.onresize =function (){
            myChart.resize()
         }

        }
      }

    }
</script>

<style scoped>
  #section_three{
    width: 100%;
    height: 500px;
    box-sizing: border-box;
    margin-bottom: 30px;
    min-width: 400px;
  }
</style>



